<template>
  <el-container style="border:2px solid #C0C0C0;border-radius:10px;height: 600px; width: 1200px; margin: auto">
    <!--  -->
    <el-header class="logo">
<el-image
      style="width: 55px; height: 55px;margin:5px;float:left"
      :src="url"
      :fit="contain"></el-image>
<div style="float:left">
</div>
    </el-header>
<el-container style="height: 500px; width: 1200px; margin: auto">
      <el-aside width="200px"
              style="background-color:rgb(238, 241, 246)">
      <br /><br />
      <el-button  @click="menu()">  </el-button>
    </el-aside>
    
      <el-main>
       <div>
       <el-form ref="form" :model="form" label-width="120px">
  <el-form-item label="" >
    <el-input v-model="form.name" placeholder=""></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input type="textarea" v-model="form.desc" placeholder="50"></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-col :span="5">
      <el-input v-model="form.score" placeholder=""></el-input>
    </el-col>
    </el-form-item>
  <el-form-item label="">
    <el-col :span="5">
      <el-input v-model="form.time" placeholder=""></el-input>
    </el-col>
  </el-form-item>
  <el-form-item label="">
    <el-switch v-model="form.fast" ></el-switch>
  </el-form-item>
  <el-form-item label="">
    <el-select v-model="form.type" placeholder="" >
      <el-option label="" value="1"></el-option>
      <el-option label="" value="2"></el-option>
      <el-option label="" value="3"></el-option>
      <el-option label="" value="4"></el-option>
      <el-option label="" value="5"></el-option>
    </el-select>
  </el-form-item>
 
  <el-form-item>
    <el-button type="primary" @click="onSubmit"></el-button>
    <el-button @click="menu()"></el-button>
  </el-form-item>
</el-form>
       </div>
      </el-main>
    </el-container>
  </el-container>
</template>

<style>
.el-header {
  background-color: #c1cbd8;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
.logo{
    font-family:"Microsoft YaHei";
	font-size:24px;
	color:#33CCFF;
	background-color:#CCFFFF
}
.mainbt{
	width: 300px;
	height: 150px;
	margin:40px
}
</style>

<script>
export default {
  name: 'UserIndex',
  data() {
      return {
        url: 'https://images.cnblogs.com/cnblogs_com/wshope/1756522/o_200504090357logo.png',
        form: {
          name: '',
          desc: '',
          score:'',
          time:'',
          fast:'',
          type:[]
        }
      }
    },
    methods:{
    menu(){
    this.$router.push('userindex');
    },
    onSubmit() {
    
    this.$notify({
          title: '',
          message: '',
          type: 'success'
        });
        console.log('submit!');
        this.$router.push('userindex');
      }
    }
    
    
}
</script>


