<template>
  <el-container style="border:2px solid #C0C0C0;border-radius:10px;height: 600px; width: 1200px; margin: auto">
    <el-header class="logo">
<el-image
      style="width: 55px; height: 55px;margin:5px;float:left"
      :src="url"
      :fit="contain"></el-image>
<div style="float:left">
</div>
    </el-header>

    <el-container >
    <el-aside width="200px"
              style="background-color: rgb(238, 241, 246)">
      <el-menu router='true'>
        <!-- router=trueindex -->
        <el-menu-item index="user">
          <i class="el-icon-setting"></i>
        </el-menu-item>
        <el-menu-item index="report">
          <i class="el-icon-setting"></i>
        </el-menu-item>
        <el-menu-item index="feedback">
          <i class="el-icon-setting"></i>
        </el-menu-item>
        <el-menu-item index="require">
          <i class="el-icon-setting"></i>
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-main>
    <div style="height:380px; width:900px">
    <div style="height:380px; width:200px; float:left">
    	<div style="height:45px">
    	<h5></h5>
        </div>
        <div style="height:100px">
    	<h5></h5>
        </div>
    	<div style="height:100px">
    	<h5></h5>
        </div>
        <div style="height:45px">
    	<h5></h5>
        </div>
        <div style="height:45px">
    	<h5></h5>
        </div>
        <div style="height:45px">
    	<h5></h5>
        </div>
    </div>
    <div style="height:380px; width:450px;float:left">
    	<div style="height:45px">
    	<b><p>{{detail.taskName}}</p></b>
        </div>
        <div style="height:100px">
    	<b><p>{{detail.taskContent}}</p></b>
        </div>
    	<div style="height:100px">
    	<b><p>{{detail.reportContent}}</p></b>
        </div>
        <div style="height:45px">
    	<b><p>{{detail.receiverName}}</p></b>
        </div>
        <div style="height:45px">
    	<b><p>{{detail.senderName}}</p></b>
        </div>
        <div style="height:45px">
    	<b><p>{{detail.reporterName}}</p></b>
        </div>
    </div>
    <div style="height:380px; width:200px;float:left">
    <el-col>
	<el-button type="primary" style="width:120px" @click="handleDelete()"></el-button>
	<br /><br />
	<el-button type="primary" style="width:120px" @click="handleUndelete()">  </el-button>
	<br /><br />
	<el-button type="primary" style="width:120px" @click="handleTrans()"></el-button>
	<br /><br />
	<el-button style="width:120px" @click="handleBack()"></el-button>
	</el-col>
    
    </div>
    </div>
    </el-main>   
  

    </el-container>
  </el-container>
</template>
<style>
.el-header {
  background-color: #c1cbd8;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}

.div-inline{ 
	display:inline;
} 
p{ 
	font-family:"Hiragino Sans GB";
	font-size:17px;
	font-weight:600;
}
.logo{
    font-family:"Microsoft YaHei";
	font-size:24px;
	color:#33CCFF;
	background-color:#CCFFFF
}

	
</style>

<script>
import axios from 'axios'  
export default {
  name: 'ReportDetail',
  data() {
      return {
      url: 'https://images.cnblogs.com/cnblogs_com/wshope/1756522/o_200504090357logo.png',
      detail:'',
      ID:''
    }
    
    },
    mounted: function () {
      this.ID=this.$route.params.id;
    },
    //
    created: function () {
    var self=this;
        axios.get('url',{params: {id:self.ID}})
           .then(response => {
              this.detail=response.data;
            })
        
    },
   methods:{
   //
      handleDelete() {
      var self=this;
      axios.get('url',{params: {id:self.detail.suitID}})
           .then(response => {
              
            });
      this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
        this.$router.push("report");
      },
      //
      handleUndelete() {
      var self=this;
      axios.get('url',{params: {id:self.detail.suitID}})
           .then(response => {
              
            });
      	this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
        this.$router.push("report");
      },
      handleTrans() {
      this.$router.push("user");
     
      },
      handleBack() {
      this.$router.push("report");
      
      }
   
   
    }
  
  
}
</script>



