<template>
  <div class="main" style="border:2px solid #C0C0C0;border-radius:10px;height: 600px; width: 1200px; margin: auto">
    <div style="width: 500px; margin: auto">
    <h3></h3>
    <el-form ref="form" :model="form" label-width="80px">
  <el-form-item label="">
    <el-input  v-model="form.name"></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input v-model="form.passwd"></el-input>
  </el-form-item>
  <el-form-item>
  <el-button type="primary" @click="onReg">   </el-button>
    <el-button type="primary" @click="onSubmit">    </el-button>
    <el-button  type="text" ></el-button>
  </el-form-item>
</el-form>

    <router-link to="/adminlogin"></router-link>
    <router-link to="/userindex"></router-link>
    </div>
  </div>
</template>

<style>
.main{
	background:-webkit-linear-gradient(
	top,#D0D0D0,#E8E8E8,lightblue
	);
}
</style>

<script>
import axios from 'axios'
export default {
  name: 'Login',
  data() {
      return {
        form: {
          name: '',
          passwd: '',
          
        },
        session:''
      }
    },
    methods: {
    //
      onSubmit() {
      var sendJson = JSON.stringify(this.form);
      var self=this;
      axios.post('login-url',sendJson)
        .then((response) => {
        self.session= response.data
          console.log(response)
        })
        .catch((error) => {
          console.log(error)
        })
        console.log('submit!');
      },
      onReg() {
      this.$router.push('register');
      }
    }
}
</script>


