<template>
  <el-container style="border:2px solid #C0C0C0;border-radius:10px;height: 600px; width: 1200px; margin: auto">
    <el-header class="logo">
<el-image
      style="width: 55px; height: 55px;margin:5px;float:left"
      :src="url"
      :fit="contain"></el-image>
<div style="float:left">
</div>
    </el-header>

    <el-container >
    <el-aside width="200px"
              style="background-color: rgb(238, 241, 246)">
      <el-menu router='true'>
        <!-- router=trueindex -->
        <el-menu-item index="report">
          <i class="el-icon-setting"></i>
        </el-menu-item>
        <el-menu-item index="feedback">
          <i class="el-icon-setting"></i>
        </el-menu-item>
        <el-menu-item index="require">
          <i class="el-icon-setting"></i>
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-main style="position:relative">
    	<el-table
    :data="tableData"
    style="width: 100%">
    <el-table-column
      label="UID"
      width="140">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.userIDNumber }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      width="100">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.userName }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      width="100">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.points }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      width="100">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.state }}</span>
      </template>
    </el-table-column>
    <el-table-column label="">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="dialogFormVisible = true"></el-button>
        <el-dialog title="" :visible.sync="dialogFormVisible">
  <el-form :model="upform">
    <el-form-item label="" :label-width="formLabelWidth">
      <el-input v-model="upform.score" autocomplete="off"></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false"> </el-button>
    <el-button type="primary" @click="handleEdit(scope.$index, scope.row)"> </el-button>
  </div>
</el-dialog>
        <el-popover
  placement="top"
  width="160"
  v-model="visible2">
  <p></p>
  <div style="text-align: right; margin: 0">
    <el-button size="mini" type="text" @click="visible2 = false"></el-button>
    <el-button type="primary" size="mini" @click="handleReset(scope.$index, scope.row)"></el-button>
  </div>
  <el-button
	  slot="reference"
          size="mini"
          @click="visible2 = true"></el-button>
</el-popover>
        <el-popover
  placement="top"
  width="160"
  v-model="visible3">
  <p></p>
  <div style="text-align: right; margin: 0">
    <el-button size="mini" type="text" @click="visible3 = false"></el-button>
    <el-button type="primary" size="mini" @click="handleDelete(scope.$index, scope.row)"></el-button>
  </div>
  <el-button
	  slot="reference"
          size="mini"
          type="danger"
          @click="visible3 = true"></el-button>
</el-popover>
          <el-popover
  placement="top"
  width="160"
  v-model="visible4">
  <p></p>
  <div style="text-align: right; margin: 0">
    <el-button size="mini" type="text" @click="visible4 = false"></el-button>
    <el-button type="primary" size="mini" @click="handleForbide(scope.$index, scope.row)"></el-button>
  </div>
  <el-button
	  slot="reference"
          size="mini"
          type="danger"
          @click="visible4 = true"></el-button>
</el-popover>
          </el-popover>
          <el-popover
  placement="top"
  width="160"
  v-model="visible5">
  <p></p>
  <div style="text-align: right; margin: 0">
    <el-button size="mini" type="text" @click="visible5 = false"></el-button>
    <el-button type="primary" size="mini" @click="handleActive(scope.$index, scope.row)"></el-button>
  </div>
  <el-button
	  slot="reference"
          size="mini"
          type="danger"
          @click="visible5 = true"></el-button>
</el-popover>
      </template>
    </el-table-column>
    
  </el-table>

  <div class="block" style="position: absolute;bottom:10px">
    <span class="demonstration"></span>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage1"
      :page-size="8"
      layout="total, prev, pager, next"
      :total="1">
    </el-pagination>
  </div>
    </el-main>   
  

    </el-container>
  </el-container>
</template>

<style>
.el-header {
  background-color: #c1cbd8;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
.logo{
    font-family:"Microsoft YaHei";
	font-size:24px;
	color:#33CCFF;
	background-color:#CCFFFF
}
</style>

<script>
import axios from 'axios'  
  
export default {
    name: 'UserManage',
    data() {
      return {
      visible1: false,
      visible2: false,
      visible3: false,
      visible4: false,
      visible5: false,
      currentPage: 1,
      url: 'https://images.cnblogs.com/cnblogs_com/wshope/1756522/o_200504090357logo.png',
        tableData:'',
        dialogFormVisible: false,
        upform: {
          score:''
        },
        formLabelWidth: '120px'
      }
    },
    //
    created: function () {
        axios.get('url')
           .then(response => {
              this.tableData = response.data
            })
        
    },
    methods: {
    //
      handleEdit(index, row) {
      var self=this;
      	this.dialogFormVisible=false;
      	axios.get('url',{params: {IDNumber:row.userIDNumber,points:row.points}})
           .then(response => {
              this.msg = response
            });
        this.$message({
          showClose: true,
          message: ''
        });
        console.log(this.msg);
      },
      //
      handleReset(index, row) {
      visible2 = false;
      	axios.get('url',{params: {id:row.userIDNumber}})
           .then(response => {
              this.msg = response
            });
        this.$message({
          showClose: true,
          message: ''
        });
        console.log(this.msg);
      },
      //
      handleDelete(index, row) {
      visible3 = false;
      axios.get('url',{params: {id:row.userIDNumber}})
           .then(response => {
              this.msg = response
            });
        this.$message({
          showClose: true,
          message: ''
        });
        console.log(this.msg);
      },
      //
      handleActive(index, row) {
      visible5 = false;
      axios.get('url',{params: {id:row.userIDNumber}})
           .then(response => {
              this.msg = response
            });
        this.$message({
          showClose: true,
          message: ''
        });
        console.log(this.msg);

      },
      //
      handleForbide(index, row) {
      visible4 = false;
      axios.get('url',{params: {id:row.userIDNumber}})
           .then(response => {
              this.msg = response
            });
        this.$message({
          showClose: true,
          message: ''
        });
        console.log(this.msg);

      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
      }
    }
  }
</script>


